<template>
  <!-- 搜索框 -->
  <div class="goodstypepage">
    <div class="goodspage">
      <el-form
        class="titleform"
        :inline="true"
        label-width="100px"
        style="line-height: 36px"
        ref="searchForm"
      >
        <el-form-item label="商品类型搜索">
          <el-input
            v-model="searchName"
            class="searchipt"
            placeholder="请输入"
          ></el-input>
        </el-form-item>
        <el-button
          class="searchbtn"
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="searchGood"
          >查询</el-button
        >
      </el-form>
    </div>
    <div class="goodsbody">
      <el-button
        type="primary"
        icon="el-icon-circle-plus"
        @click="dialogVisible = true"
        class="addbtn"
        >新建</el-button
      >
      <el-dialog title="新增商品类型" :visible.sync="dialogVisible" width="40%">
        <el-form
          label-width="120px"
          :inline="true"
          :rules="rules"
          :model="form"
          ref="form"
        >
          <el-form-item label="商品类型名称" prop="className" />
          <el-input
            v-model.trim="form.className"
            maxlength="10"
            show-word-limit
          ></el-input>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancel">取 消</el-button>
          <el-button type="primary" @click="addGoodFn">确 定</el-button>
        </span>
      </el-dialog>
      <template>
        <el-table
          :data="list"
          style="width: 100% font-size:10px"
          class="tablebox"
          :header-cell-style="{ background: '#f3f6fb' }"
        >
          <el-table-column label="序号" width="80">
            <template slot-scope="scope">
              {{ getIndex(scope.row.classId) + 1 }}
            </template>
          </el-table-column>
          <el-table-column
            ref="gooditem"
            prop="className"
            label="商品类型名称"
            width="980"
          >
          </el-table-column>
          <el-table-column prop="address" label="操作" width="100">
            <template slot-scope="scope">
              <div class="actions">
                <el-button
                  type="text"
                  style="color: #4368e6"
                  @click="editGood(scope.row.classId, scope.row.className)"
                  >修改</el-button
                >
                <el-button
                  type="text"
                  style="color: #ff5a5a"
                  @click="delFn(scope.row.classId)"
                  >删除</el-button
                >
              </div>
            </template>
          </el-table-column>
        </el-table>
        <el-dialog
          title="修改商品类型"
          :visible.sync="dialogVisibleTwo"
          width="60%"
        >
          <el-form
            label-width="120px"
            :inline="true"
            :rules="rules"
            :model="editForm"
            ref="editform"
          >
            <el-form-item label="商品类型名称" prop="className" />
            <el-input
              v-model="editForm.className"
              maxlength="10"
              show-word-limit
            ></el-input>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button @click="editCancel">取 消</el-button>
            <el-button @click="editSubmit" type="primary">确 定</el-button>
          </span>
        </el-dialog>
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="pageIndex"
          :page-size="pageSize"
          background
          layout="total,prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </template>
    </div>
  </div>
</template>

<script>
import {
  getGoodList,
  addGoods,
  editGoods,
  searchGoods,
  delGoods
} from '@/api/good'
// import TitleCom from '@/components/TitleCom.vue'
export default {
  // components: { TitleCom },
  name: 'GoodsType',
  data () {
    return {
      // 第一个弹出框
      dialogVisible: false,
      // 第二个弹出框
      dialogVisibleTwo: false,
      // 页码
      pageIndex: 1,
      // 每页显示的个数
      pageSize: 10,
      // 总共的页数
      totalPage: '',
      // 总共的个数
      total: 0,
      // 得到的全部数组
      list: [],
      // 搜索得到的数组
      searchList: [],
      // 搜索的关键字
      searchName: '',
      // 第一个表单绑定的数据
      form: {
        className: ''
      },
      // 第二个表单绑定的数据
      editForm: {
        className: '',
        id: 0
      },
      // 校验规则
      rules: {
        className: [
          {
            required: true,
            message: '请输入标题',
            trigger: ['blur', 'change']
          }
        ]
      }
    }
  },
  created () {
    this.getList()
  },
  methods: {
    async getList () {
      const res = await getGoodList({
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      })
      // console.log(res)
      this.list = res.currentPageRecords
      this.total = parseInt(res.totalCount)
    },
    handleCurrentChange (val) {
      this.pageIndex = val
      // console.log(val)
      this.getList()
    },
    getIndex (id) {
      return (
        this.list.findIndex((item) => item.classId === id) +
        (this.pageIndex - 1) * 10
      )
    },
    // 修改数据 把id传过来
    async editGood (id, name) {
      this.dialogVisibleTwo = true
      this.editForm.id = id
      this.editForm.className = name
      // console.log(this.$refs.gooditem)
    },
    // 修改商品
    async editSubmit () {
      await editGoods(this.editForm.id, { className: this.editForm.className })
      // console.log(res)
      this.editCancel()
      this.getList()
    },
    // 新增商品
    async addGoodFn () {
      try {
        await addGoods(this.form)
        this.$message.success('添加成功')
      } catch (err) {
        this.$message.error(err.response.data)
        // console.log(err)
      }
      this.pageIndex = 1
      this.getList()
      this.cancel()
      // console.log(res)
    },
    // 搜索商品
    async searchGood () {
      const test = {
        pageIndex: this.pageIndex,
        pageSize: this.pageSize,
        className: this.searchName
      }
      const res = await searchGoods(test)
      // console.log(res)
      this.list = res.currentPageRecords
      this.pageIndex = parseInt(res.pageIndex)
      this.pageSize = parseInt(res.pageSize)
      this.totalPage = res.totalPage
      this.total = parseInt(res.totalCount)
    },
    // 第一个对话框点击关闭窗口
    cancel () {
      this.dialogVisible = false
      this.$refs.form.resetFields()
    },
    // 第二个对话框关闭
    editCancel () {
      this.dialogVisibleTwo = false
      this.$refs.editform.resetFields()
    },
    // 删除数据
    async delFn (id) {
      // this.$message.warning('没有权限删除')
      try {
        await delGoods(id)
        this.$message.success('删除成功')
      } catch (err) {
        this.$message.error(err.response.data)
      }
      this.getList()
    }
  }
}
</script>

